import React, { useEffect, MouseEvent } from "react";
import { BrowserRouter, Switch, Route, NavLink, Link } from "react-router-dom";
import starida from "../assets/Starida.webp";
import "../CSS/Navbar.css";
import $, { data } from "jquery";
import Home from "../components/Home";
import wapi from "../useful/weatherapi";

const Navbar = () => {
  useEffect(() => {
    const lat = 53.37;
    const long = -4.1;
    const params =
      "secondarySwellDirection,secondarySwellHeight,secondarySwellPeriod,windWaveDirection,windWaveHeight,windWavePeriod,swellDirection,swellHeight,swellPeriod,waveDirection,waveHeight,wavePeriod,windDirection,windSpeed,gust,waterTemperature";

    const weatherData = fetch(
      `https://stormglass.p.rapidapi.com/forecast?lat=${lat}&lng=${long}&params=${params}`,
      {
        method: "GET",
        headers: {
          "x-rapidapi-host": "stormglass.p.rapidapi.com",
          "x-rapidapi-key": `${wapi}`,
        },
      }
    )
      .then((response) => response.json())
      .then(function (data) {
        console.log(data);
        const swellHeight = `${data.hours[12].swellHeight[5].value}m`;
        const swellPeriod = `${data.hours[12].swellPeriod[5].value}s`;
        const waveHeight = `${data.hours[12].waveHeight[5].value}m`;
        const wavePeriod = `${data.hours[12].wavePeriod[4].value}s`;
        const windSpeed = `${data.hours[12].windSpeed[0].value}mph`;
        const windDirectionT = data.hours[12].windDirection[0].value;
        let direction;
        if (
          parseInt(windDirectionT) >= 348.75 &&
          parseInt(windDirectionT) < 11.25
        ) {
          direction = "N";
        } else if (
          parseInt(windDirectionT) >= 11.25 &&
          parseInt(windDirectionT) < 33.75
        ) {
          direction = "NNE";
        } else if (
          parseInt(windDirectionT) >= 37.75 &&
          parseInt(windDirectionT) < 56.25
        ) {
          direction = "NE";
        } else if (
          parseInt(windDirectionT) >= 56.25 &&
          parseInt(windDirectionT) < 78.75
        ) {
          direction = "ENE";
        } else if (
          parseInt(windDirectionT) >= 78.75 &&
          parseInt(windDirectionT) < 101.25
        ) {
          direction = "E";
        } else if (
          parseInt(windDirectionT) >= 101.25 &&
          parseInt(windDirectionT) < 123.75
        ) {
          direction = "ESE";
        } else if (
          parseInt(windDirectionT) >= 123.75 &&
          parseInt(windDirectionT) < 146.25
        ) {
          direction = "SE";
        } else if (
          parseInt(windDirectionT) >= 146.25 &&
          parseInt(windDirectionT) < 168.75
        ) {
          direction = "SSE";
        } else if (
          parseInt(windDirectionT) >= 168.75 &&
          parseInt(windDirectionT) < 191.25
        ) {
          direction = "S";
        } else if (
          parseInt(windDirectionT) >= 191.25 &&
          parseInt(windDirectionT) < 213.75
        ) {
          direction = "SSW";
        } else if (
          parseInt(windDirectionT) >= 213.75 &&
          parseInt(windDirectionT) < 236.25
        ) {
          direction = "SW";
        } else if (
          parseInt(windDirectionT) >= 236.25 &&
          parseInt(windDirectionT) < 258.75
        ) {
          direction = "WSW";
        } else if (
          parseInt(windDirectionT) >= 258.75 &&
          parseInt(windDirectionT) < 281.25
        ) {
          direction = "W";
        } else if (
          parseInt(windDirectionT) >= 281.25 &&
          parseInt(windDirectionT) < 303.75
        ) {
          direction = "WNW";
        } else if (
          parseInt(windDirectionT) >= 303.75 &&
          parseInt(windDirectionT) < 326.25
        ) {
          direction = "NW";
        } else if (
          parseInt(windDirectionT) >= 326.25 &&
          parseInt(windDirectionT) < 348.75
        ) {
          direction = "NNW";
        }

        const windDirection = `${data.hours[12].windDirection[0].value} (${direction})`;
        const gust = `${data.hours[12].gust[0].value}mph`;
        const waterTemp = `${data.hours[12].waterTemperature[2].value} °C`;

        const seaData = [
          swellHeight,
          swellPeriod,
          waveHeight,
          wavePeriod,
          windSpeed,
          windDirection,
          gust,
          waterTemp,
        ];

        for (let i = 0; i < seaData.length; i++) {
          let li = document.querySelectorAll(".apili");
          const lidata = document.createTextNode(` ${seaData[i]}`);
          li[i].appendChild(lidata);
        }
      });
  });

  useEffect(() => {
    const hamburger = document.querySelector(".hamburgers");
    const overlay = document.getElementById("overlay");
    const item = document.querySelectorAll(".overli");
    const lines = document.querySelector(".hamburgers");

    hamburger?.addEventListener("click", function () {
      overlay?.classList.toggle("active");
      item?.forEach((item) => {
        item.classList.toggle("active");
      });
      lines?.classList.toggle("active");
    });
  });

  return (
    <div>
      <header>
        <nav>
          <div className="navbar">
            <img src={starida} alt="Starida Logo" className="logo" />
            <ul className="api">
              <li className="apili">Swell Height :</li>
              <li className="apili">Swell Period :</li>
              <li className="apili">Wave Height :</li>
              <li className="apili">Wave Period :</li>
              <li className="apili">Wind Speed :</li>
              <li className="apili">Wind Direction :</li>
              <li className="apili">Gust :</li>
              <li className="apili">Water Temperature :</li>
            </ul>
            <div className="hamburgers" id="hamburger">
              <div className="hamburger"></div>
            </div>
            <div className="overlay" id="overlay">
              <ul>
                <Link to={"/"}>
                  <li className="overli">Home</li>
                </Link>
                <Link to={"/Cruises"}>
                  <li className="overli">Cruises</li>
                </Link>
                <Link to={"/Ftrips"}>
                  <li className="overli">Fishing Trips</li>
                </Link>
                <Link to={"/Gallery"}>
                  <li className="overli">Gallery</li>
                </Link>
                <Link to={"/Faq"}>
                  <li className="overli">FAQ's</li>
                </Link>
                <Link to={"/Contact"}>
                  <li className="overli">Contact</li>
                </Link>
              </ul>
            </div>
          </div>
        </nav>
      </header>
    </div>
  );
};

export default Navbar;
